module.exports=function (canvas,percent,bgColor) {

    function Circle() {
        this.radius = 214;    // 
        this.lineWidth = 12;  // 
        this.strokeStyle = '#ccc'; //
        this.fillStyle = bgColor;  //
        this.lineCap = 'round';
    }

    Circle.prototype.draw = function(ctx) {
        ctx.beginPath();
        ctx.arc(220, 220, this.radius, 0, Math.PI*2, true);  // 2500Math.PI*2
        ctx.lineWidth = this.lineWidth;
        ctx.strokeStyle = this.strokeStyle;
        ctx.stroke();  // strokefill
    }

    function Ring(startAngle, percent) {
        Circle.call(this);
        this.startAngle = startAngle; //
        this.percent = percent;  //
    }

    Ring.prototype = Object.create(Circle.prototype);

    Ring.prototype.drawRing = function(ctx) {
        this.draw(ctx);  // Circledraw

        // angle
        ctx.beginPath();
        var anglePerSec = 2 * Math.PI*(this.percent /100 ); // 
        ctx.arc(220, 220, this.radius, this.startAngle ,anglePerSec, false); //cirle
        ctx.font="56px PingFang";
        ctx.fillStyle=bgColor;
        ctx.fillText(this.percent+"%",140,250);
        ctx.strokeStyle = this.fillStyle;
        ctx.lineCap = this.lineCap;
        ctx.stroke();
        ctx.closePath();
    };

    var ctx =  canvas.getContext('2d');
    var ring = new Ring(0, percent);  // 2*Math.PI/350%
    ring.drawRing(ctx);


}



