<!---->
<style>
    #add-finance textarea {
        width: 100%;
        resize: none;
        height: 100px;
    }

    #add-finance .weui_cell:before, #houseList .weui_cell:before {
        left: 0px;
    }

    #add-finance .weui_cells, #houseList .weui_cells {
        margin-top: 6px;
    }

    #add-finance .weui_cells p span {
        margin-right: 6px;
    }

    #add-finance .weui_cells p input {
        width: calc(100% - 90px);
    }

    #add-finance .wrapper, #houseList .wrapper {
        position: absolute;
        width: 100%;
        z-index: 20;
        top: 64px;
        left: 0px;
        bottom: 0px;
        overflow: hidden;
    }

    #houseList .wrapper {
        bottom: 24px;
    }

    #houseList {
        position: fixed;
        background: rgb(255, 255, 255);
        width: 100%;
        height: 100%;
        z-index: 9999;
    }
</style>
<template>
    <div id="add-finance">
        <header>
            <a v-on:touchend="closeBack()" class="header-back"></a>
            
            <a v-on:touchend="save()" class="header-btn"></a>
        </header>
        <div class="wrapper">
            <div class="weui_cells  xsdApp-bg-white">
                <div class="weui_cell">
                    <div class="weui_cell_bd weui_cell_primary">
                        <p class="xsdApp-font-size15 xsdApp-icon-right">
                            <span>:</span>
                            <input id="flow-picker" type="text" placeholder="">
                        </p>
                    </div>
                </div>
                <div class="weui_cell xsdApp-bg-gray">
                    <div class="weui_cell_bd weui_cell_primary">
                        <p class="xsdApp-font-size15"></p>
                    </div>
                </div>
                <div v-if="flag">
                    <div class="weui_cell">
                        <div class="weui_cell_bd weui_cell_primary">
                            <p class="xsdApp-font-size15"><span>:</span><input id="apartmentName"
                                                                                   type="text"
                                                                                   placeholder="" readonly>
                            </p>
                        </div>
                    </div>
                    <div class="weui_cell">
                        <div class="weui_cell_bd weui_cell_primary">
                            <p class="xsdApp-font-size15"><span>:</span><input id="floorName" type="text"
                                                                                   readonly placeholder=""
                            ></p>
                        </div>
                    </div>
                    <div class="weui_cell">
                        <div class="weui_cell_bd weui_cell_primary">
                            <p class="xsdApp-font-size15 xsdApp-icon-right"><span>:</span><input
                                    id="aparmentRoomList"
                                    type="text"
                                    readonly
                                    placeholder="">
                            </p>
                        </div>
                    </div>
                </div>
                <div v-else>
                    <div class="weui_cell">
                        <div class="weui_cell_bd weui_cell_primary">
                            <p class="xsdApp-font-size15"><span>:</span><input v-on:touchend="openList"
                                                                                   v-model="housName" type="text"
                                                                                   placeholder="" readonly>
                            </p>
                        </div>
                    </div>
                    <div class="weui_cell">
                        <div class="weui_cell_bd weui_cell_primary">
                            <p class="xsdApp-font-size15 xsdApp-icon-right"><span>:</span><input id="roomList"
                                                                                                     type="text"
                                                                                                     placeholder="">
                            </p>
                        </div>
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_bd weui_cell_primary">
                        <p class="xsdApp-font-size15"><span>:</span><input v-model="trader_name" type="text"
                                                                               placeholder=""></p>
                    </div>
                </div>
                <div class="weui_cell xsdApp-bg-gray">
                    <div class="weui_cell_bd weui_cell_primary">
                        <p class="xsdApp-font-size15"></p>
                    </div>
                </div>
                <!--<div class="weui_cell">-->
                <!--<div class="weui_cell_bd weui_cell_primary">-->
                <!--<p class="xsdApp-font-size15 xsdApp-icon-right"><span>:</span><input id="fees-picker" type="text" placeholder=""></p>-->
                <!--</div>-->
                <!--</div>-->
                <div class="weui_cell">
                    <div class="weui_cell_bd weui_cell_primary">
                        <p class="xsdApp-font-size15 xsdApp-icon-right"><span>:</span><input id="fees-picker"
                                                                                                 type="text"
                                                                                                 placeholder="">
                        </p>
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_bd weui_cell_primary">
                        <p class="xsdApp-font-size15"><span>:</span><input v-model="fees" type="number"
                                                                               placeholder=""></p>
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_bd weui_cell_primary">
                        <p class="xsdApp-font-size15 xsdApp-icon-right"><span>:</span><input id="payMethod"
                                                                                                 type="text"
                                                                                                 placeholder="">
                        </p>
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_bd weui_cell_primary">
                        <p class="xsdApp-font-size15 xsdApp-icon-right"><span>:</span><input id="pay-time"
                                                                                                 type="text"
                                                                                                 placeholder="">
                        </p>
                    </div>
                </div>
                <div class="weui_cell xsdApp-bg-gray">
                    <div class="weui_cell_bd weui_cell_primary">
                        <p class="xsdApp-font-size15"></p>
                    </div>
                </div>
                <div class="weui_cell">
                    <div class="weui_cell_bd weui_cell_primary">
                        <textarea v-model="remark" placeholder=""></textarea>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div id="houseList" class="xsdApp-hide  xsdApp-bg-gray">
        <header>
            <a v-on:touchend="close()" class="header-back"></a>
            
        </header>
        <div class="wrapper">
            <div>
                <div class="weui_cells  xsdApp-bg-white" v-for="house in houses" v-on:click="getHouse(house)">
                    <div class="weui_cell">
                        <div class="weui_cell_bd weui_cell_primary">
                            <p class="xsdApp-font-size15">{{house.attributes.location}}</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

    export default{
        data(){
            return {
                housName: "",
                flow_type: "",
                trader_name: "",
                house_id: "",
                room_id: "",
                remark: "",
                fees: "",
                houses: "",
                rooms: [],
                target: this.$route.query.target,
                flag: flag,
                apartmentList: {},
                apartments: [],
                floors: []
            }
        },

        methods: {
            /**
             * 
             */
            closeBack: function () {
                $(".close-picker").trigger("click");
                if (this.target == "smart") {
                    appObj.closeFrame()
                } else {
                    this.$router.go({
                        name: 'finance',
                    })
                }
            },

            openList: function () {
                $.showLoading('');
                this.$http.get(baseUrl + "api/v1/houses?search=" + "", {}, {
                    headers: {
                        "Accept": "application/vnd.api+json",
                        "Content-Type": "application/vnd.api+json"
                    }
                }).then(function (response) {
                    $.hideLoading();
                    this.$set('houses', response.data.data);
                    $("#houseList").removeClass("xsdApp-hide fadeOutRightBig").addClass("animated fadeInRightBig");
                    var intervalTime = setInterval(function () {
                        var resultContentH = $("#houseList .wrapper").height();
                        if (resultContentH > 0) {
                            $("#showResult").height(resultContentH)
                            setTimeout(function () {
                                clearInterval(intervalTime);
                                var myScroll1 = new IScroll('#houseList .wrapper', {
                                    click: true
                                });
                            }, 100)
                        }
                    }, 20);
                }.bind(this))
            },
            /**
             * 
             * @param obj
             */
            getHouse: function (obj) {
                this.$set("housName", obj.attributes.location);
                this.$set("house_id", obj.id);
                let _this = this;
                $.each(obj.attributes.rooms_identifiers, function (i, value) {
                    let data = {};
                    data.title = value.name;
                    data.value = value.id;
                    _this.rooms.push(data);
                });
                $("#houseList").removeClass("fadeInRightBig").addClass("animated fadeOutRightBig");
                $("#roomList").select({
                    title: "",
                    autoClose: true,
                    items: _this.rooms
                });
            },
            /**
             * 
             */
            close: function () {
                $("#houseList").removeClass("fadeInRightBig").addClass("animated fadeOutRightBig");
            },

            /**
             * 
             */
            save: function () {
                let data = {
                    "flow_type": $("#flow-picker").attr("data-values"),
                    "trader_name": this.trader_name,
                    "room_id": $("#roomList").attr("data-values"),
                    "pay_method": $("#payMethod").attr("data-values"),
                    "trade_at": $("#pay-time").val(),
                    "remark": this.remark
                };
                if (flag) {
                    data["apartment"] = this.house_id;
                    data["room"] = $("#aparmentRoomList").attr("data-values");
                } else {
                    data["house_id"] = this.house_id;
                    data["room_id"] = $("#roomList").attr("data-values");
                }
                data[$("#fees-picker").attr("data-values")] = this.fees;

                this.$http.post(baseUrl + "api/v2/moneyflows", data, {
                    emulateJSON: true,
                    headers: {
                        "Accept": "*/*",
                        "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
                    }
                }).then(function (response) {
                    $.toast("", "success");
                    this.closeBack();

                }.bind(this)).catch(function (err) {
                    if (err.status == "400") {
                        $.toast(err.data.errors.detail, "forbidden");
                    } else {
                        $.toast("", "forbidden");
                    }
                })

            }
        },

        ready: function () {

            let that = this;

            if (flag) {
                this.$http.get(baseUrl + "api/v1/apartmentinds", {}, {
                    headers: {
                        "Accept": "application/vnd.api+json",
                        "Content-Type": "application/vnd.api+json"
                    }
                }).then(function (response) {
                    let _this = this;
                    _this.apartmentList = response.data.data.attributes.apartments;
                    $.each(_this.apartmentList, function (i, value) {
                        let data = {};
                        data.title = value.name;
                        data.value = i;
                        _this.apartments.push(data);

                    });
                    $("#apartmentName").select({
                        title: "",
                        autoClose: true,
                        items: _this.apartments,
                        onChange: function () {

                        }
                    });
                }).bind(this);

                $("#apartmentName").change(function () {

                    let num = parseInt($("#apartmentName").attr("data-values"));
                    that.house_id = that.apartmentList[num].id;
                    $.each(that.apartmentList[num].floors, function (i, value) {
                        let data = {};
                        data.title = value.name;
                        data.value = value.id;
                        that.floors.push(data);
                    });
                    $("#floorName").select({
                        title: "",
                        autoClose: true,
                        items: that.floors,
                    });
                });

                $("#floorName").change(function () {

                    that.$http.get(baseUrl + "api/v1/concentratedhouses?apartment=" + that.house_id + "&floor=" + $("#floorName").attr("data-values"), {}, {
                        headers: {
                            "Accept": "application/vnd.api+json",
                            "Content-Type": "application/vnd.api+json"
                        }
                    }).then(function (response) {
                        let _this = that;
                        $.each(response.data.data[0].attributes.rooms, function (i, value) {
                            let data = {};
                            data.title = value.name;
                            data.value = value.id;
                            _this.rooms.push(data);

                        });
                        $("#aparmentRoomList").select({
                            title: "",
                            autoClose: true,
                            items: _this.rooms
                        });
                    }).bind(that)
                })
            }

            $("#flow-picker").select({
                title: "",
                autoClose: true,
                items: [
                    {
                        title: "",
                        value: "0",
                    },
                    {
                        title: "",
                        value: "1",
                    }
                ]
            });
            $("#fees-picker").select({

                title: "",
                autoClose: true,
                items: [
                    {
                        title: "",
                        value: "water_fees"
                    },
                    {
                        title: "",
                        value: "power_fees"
                    },
                    {
                        title: "",
                        value: "gas_fees"
                    },
                    {
                        title: "",
                        value: "property_fees"
                    },
                    {
                        title: "",
                        value: "service_fees"
                    },
                    {
                        title: "",
                        value: "upkeep_fees"
                    },
                    {
                        title: "",
                        value: "cleaning_fees"
                    },
                    {
                        title: "",
                        value: "material_fees"
                    },
                    {
                        title: "",
                        value: "catv_fees"
                    },
                    {
                        title: "",
                        value: "internet_fees"
                    },
                    {
                        title: "",
                        value: "sanitation_fees"
                    },
                    {
                        title: "",
                        value: "other_fees"
                    }
                ]
            });

            $("#payMethod").select({
                title: "",
                autoClose: true,
                items: [
                    {
                        title: "",
                        value: "0",
                    },
                    {
                        title: "",
                        value: "1",
                    },
                    {
                        title: "",
                        value: "2",
                    },
                    {
                        title: "",
                        value: "3",
                    },
                    {
                        title: "",
                        value: "4",
                    }
                ]
            });

            $("#pay-time").datetimePicker({
                title: ""
            });
            var myScroll = new IScroll('#add-finance .wrapper', {touchmove: true});

        }
    }
</script>



